* {
  margin: 0;
  padding: 0;
}

.container {
  background: linear-gradient(to left top, #fff, #FFAC94);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper {
  background-color: #fff;
  min-width: 450px;
  min-height: 650px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000000;
  margin-bottom: 10px;
}

.header h1 {
  color: #5B5858;
  text-align: center;
  margin: 30px 0;
}

.task-input {
  outline: none;
  width: 250px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: #5B5858;
  background-color: #F7F7F7;
  border: 1px solid #ccc;
  border-radius: 10px;
}

.button-add {
  width: 100px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #FFAC94;
  cursor: pointer;
  color: #F7F7F7;
}

.list-item {
  display: flex;
  margin: 20px 0;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  max-height: 30px;
}

.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete {
  color: #ff6c6c;
  margin-right: 10px;
}

.button-edit {
  color: #e2d029;
  margin-right: 10px;
}

.button-delete {
  color: lightseagreen;
}

.complete {
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #ff6c6c;
  opacity: 0.6;
}

.list {
  width: 230px;
  background-color: #fff;
  border: none;
  color: #ccc;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}

input:focus,
button:focus {
  outline: none;
}
